@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,400,500,700);
@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900);

.nav {
  display: flex;
  align-items: center; }
  .nav__link {
    display: inline-block;
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    line-height: 1.375rem;
    font-weight: bold;
    color: #fff; }
    .nav__link + .nav__link {
      margin-right: 8.75rem; }

.header {
  background-color: #fff;
  padding: 0.75rem 0; }
  .header--theme-light {
    background-color: #fff; }
  .header--theme-gray {
    background-color: #707070; }
  .header__main {
    display: flex;
    align-items: center;
    justify-content: space-between; }

.nav-bottom {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-bottom: 3.5rem; }
  .nav-bottom__group {
    display: flex;
    flex-direction: column; }
  .nav-bottom__group-title {
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    line-height: 1.3125rem;
    font-weight: bold;
    color: #fff; }
  .nav-bottom__link {
    font-family: 'Ubuntu', sans-serif;
    font-size: 0.875rem;
    line-height: 1.1875rem;
    color: #6A6A6A;
    margin-top: 0.5rem; }

.footer {
  padding-top: 1.75rem;
  padding-bottom: 1.25rem; }
  .footer--theme-gray {
    background-color: #242424; }
  .footer--theme-light {
    background-color: #fff; }
    .footer--theme-light .nav-bottom__group-title {
      color: #6A6A6A; }


.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 0.5rem;
  padding-right: 0.5rem; }

.container--normal {
  max-width: 86.125rem; }

.container--wide {
  max-width: 103.5rem; }

.text {
  font-family: 'Ubuntu', sans-serif;
  font-size: 1.125rem;
  line-height: 1.5rem;
  font-weight: 600;
  color: #2A2A2A; }

.text-center {
  text-align: center; }

.text-left {
  text-align: left; }

.text-right {
  text-align: right; }

.text-justify {
  text-align: justify; }

html {
  box-sizing: border-box;
  font-size: 100%; }

html,
body {
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0; }

body {
  font-family: 'Ubuntu', 'Roboto', sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 400; }

a {
  color: inherit;
  text-decoration: none; }

ul {
  list-style: none; }

img {
  max-width: 100%;
  display: block;
  height: auto; }

button {
  border: none; }

button:hover {
  cursor: pointer; }

p {
  padding-bottom: 1.375rem; }

.copyright {
  font-family: 'Ubuntu', sans-serif;
  font-size: 0.875rem;
  line-height: 1.1875rem;
  color: #707070;
  text-align: right;
  padding-bottom: 0; }

